import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import { fetchImages } from './pixabay-api';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const gallery = document.querySelector('.gallery');
const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
const loadMoreButton = document.querySelector('.load-more');
const loadingOverlay = document.getElementById('loading-overlay');
const endMessage = document.createElement('p');
endMessage.classList.add('end-message');
endMessage.textContent = "We're sorry, but you've reached the end of search results.";
endMessage.style.display = 'none';
gallery.after(endMessage);

let searchQuery = '';
let currentPage = 1;
const perPage = 40;
let totalHits = 0;
let loadedImageIds = new Set();

// Ð¡ÐºÑ€Ñ‹Ð²Ð°ÐµÐ¼ ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð¸ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ Ð¿Ñ€Ð¸ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÐµ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹
loadMoreButton.style.display = 'none';
endMessage.style.display = 'none';
gallery.innerHTML = ''; loadMoreButton.style.display = 'none'; endMessage.style.display = 'none'; endMessage.style.display = 'none'; loadMoreButton.style.display = 'none'; // ÐžÑ‡Ð¸ÑÑ‚ÐºÐ° Ð³Ð°Ð»ÐµÑ€ÐµÐ¸ Ð¿Ñ€Ð¸ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÐµ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹

export async function renderImages(images, append = false) {
  if (!Array.isArray(images) || images.length === 0) {
    return;
  }

  if (!append) {
    gallery.innerHTML = ''; loadMoreButton.style.display = 'none'; endMessage.style.display = 'none'; endMessage.style.display = 'none'; loadMoreButton.style.display = 'none';
    loadedImageIds.clear();
  }

  const markup = images.map(({ webformatURL, largeImageURL, tags, likes, views, comments, downloads }) => `
    <div class="gallery-item">
      <a href="${largeImageURL}">
        <img src="${webformatURL}" alt="${tags}" loading="lazy" />
      </a>
      <div class="image-info">
        <div class="item"><span class="label">Likes</span><span class="count">${likes}</span></div>
        <div class="item"><span class="label">Views</span><span class="count">${views}</span></div>
        <div class="item"><span class="label">Comments</span><span class="count">${comments}</span></div>
        <div class="item"><span class="label">Downloads</span><span class="count">${downloads}</span></div>
      </div>
    </div>`).join('');

  gallery.insertAdjacentHTML('beforeend', markup);
  lightbox.refresh();

  // ÐŸÑ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ° ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹ Ð¿Ð¾ÑÐ»Ðµ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÐ¸ Ð½Ð¾Ð²Ñ‹Ñ… Ð¸Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ð¹
  if (append) {
    const firstGalleryItem = document.querySelector('.gallery-item');
    if (firstGalleryItem) {
      const cardHeight = firstGalleryItem.getBoundingClientRect().height;
      window.scrollBy({ top: cardHeight * 2, behavior: 'smooth' });
    }
  }

  if (gallery.children.length >= totalHits) {
    loadMoreButton.style.display = 'none';
    if (gallery.children.length > 0) { if (gallery.children.length > 0 && gallery.children.length >= totalHits) { endMessage.style.display = 'block'; } }
  } else {
    loadMoreButton.style.display = 'block';
    endMessage.style.display = 'none';
  }
}

const searchForm = document.querySelector('.search-form');
const searchInput = document.querySelector('input[name="searchQuery"]');

if (searchForm && searchInput) {
  searchForm.addEventListener('submit', async event => {
    if (event.defaultPrevented) return; // ÐŸÑ€ÐµÐ´Ð¾Ñ‚Ð²Ñ€Ð°Ñ‰Ð°ÐµÐ¼ Ð´ÑƒÐ±Ð»Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ð·Ð°Ð¿Ñ€Ð¾ÑÐ¾Ð²
    event.preventDefault();
    event.preventDefault();
    searchQuery = searchInput.value?.trim();

    if (!searchQuery) {
      console.error('Invalid search input:', searchQuery);
      return;
    }

    currentPage = 1;
    loadedImageIds.clear();
    loadMoreButton.style.display = 'none';
    endMessage.style.display = 'none';
    gallery.innerHTML = ''; loadMoreButton.style.display = 'none'; endMessage.style.display = 'none'; endMessage.style.display = 'none'; loadMoreButton.style.display = 'none'; // ÐžÑ‡Ð¸ÑÑ‚ÐºÐ° Ð¿ÐµÑ€ÐµÐ´ Ð½Ð¾Ð²Ñ‹Ð¼ Ð·Ð°Ð¿Ñ€Ð¾ÑÐ¾Ð¼

    const response = await fetchImages(searchQuery, currentPage, perPage);
    if (response && response.hits.length > 0) {
      totalHits = Math.min(response.totalHits, 500);
      await renderImages(response.hits);
      if (gallery.children.length < totalHits) {
        loadMoreButton.style.display = 'block';
      }
    }
  });
} else {
  console.error('Search form or input not found in DOM');
}

loadMoreButton.addEventListener('click', async () => {
    if (event.defaultPrevented) return; // ÐŸÑ€ÐµÐ´Ð¾Ñ‚Ð²Ñ€Ð°Ñ‰Ð°ÐµÐ¼ Ð´ÑƒÐ±Ð»Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ ÐºÐ»Ð¸ÐºÐ¾Ð²
    event.preventDefault();
  if (gallery.children.length >= totalHits) {
    loadMoreButton.style.display = 'none';
    if (gallery.children.length > 0) { if (gallery.children.length > 0 && gallery.children.length >= totalHits) { endMessage.style.display = 'block'; } }
    return;
  }

  currentPage += 1;
  showLoader();

  const response = await fetchImages(searchQuery, currentPage, perPage);
  if (response && response.hits.length > 0) {
    await renderImages(response.hits, true);
  } else {
    loadMoreButton.style.display = 'none';
    if (gallery.children.length > 0) { if (gallery.children.length > 0 && gallery.children.length >= totalHits) { endMessage.style.display = 'block'; } }
  }
  hideLoader();
});

function showLoader() {
  loadingOverlay.style.display = 'block';
}

function hideLoader() {
  loadingOverlay.style.display = 'none';
}



















